.LogoSearch {
   display: flex;
   gap: 0.75rem;
}

.LogoSearch > img {
   width: 50px;
   height: 50px;
}

.Search {
   display: flex;
   background-color: var(--inputColor);
   border-radius: 10px;
   padding: 5px;
}

.Search>input {
   background-color: transparent;
   border: none;
   outline: none;
}

.s-icon {
   display: flex;
   align-items: center;
   justify-content: center;
   background: linear-gradient(106.23deg, #009fc7 0%, blue 100%);
   border-radius: 5px;
   padding: 4px;
   color: white;
}